<template>
  <section class="previous-addresses">
    <div class="previous-addresses__header">
      <Text
        type="muted"
        uppercase
        size="14px"
      >
        {{ t('withdrawals.previousAddresses') }}
      </Text>
      <ButtonLink class="previous-addresses__header-add" @click="onShowAddForm">
        <Icon name="plus" />
        <div>
          {{ t('common.new') }} <Text lowercase>{{ t('common.contact') }}</Text>
        </div>
      </ButtonLink>
    </div>
    <Input
      hide-shadow
      :placeholder="t('common.search')"
    >
      <template #prepend>
        <Icon name="search" opacity="0.33" />
      </template>
    </Input>
    <AddAddress v-if="showAddForm" />
    <div class="previous-addresses__list">
      <div
        v-for="item in addressesList"
        :key="item.id"
        class="previous-addresses__list__item"
      >
        <Icon :name="item.crypto + '-circle'" />
        <div class="previous-addresses__list__item-info">
          <Text variant="p" mb="7px">{{ item.name }}</Text>
          <Text type="muted">{{ item.address }}</Text>
        </div>
        <Container background-color="white" box-shadow class="disable-btn">
          <Icon name="ban" opacity="0.4" />
        </Container>
      </div>
    </div>
  </section>
</template>

<script>
import Text from '@/shared/components/UIKit/Text/Text'
import ButtonLink from '@/shared/components/UIKit/ButtonLink'
import Icon from '@/shared/components/Icon/Icon'
import Input from '@/shared/components/UIKit/Input'
import { useI18n } from '@/shared/plugins/i18n'
import AddAddress from '@/admin/views/treasury/Withdrawals/AddAddress'
import { computed, ref } from 'vue'
import Container from '@/shared/components/Containers/Container'
export default {
  name: 'PreviousAddressesList',
  components: { Container, AddAddress, Input, Icon, ButtonLink, Text },
  setup () {
    const { t } = useI18n()
    const showAddForm = ref(false)
    const addressesList = computed(() => {
      const arr = []
      for (let i = 0; i < 10; i++) {
        arr.push({
          id: i + new Date(),
          name: 'John Doe',
          address: 'c15pJxcAIzaSyB9puDplMxJ62vYlFruxmmIAez3',
          crypto: 'btc'
        })
      }
      return arr
    })

    const onShowAddForm = () => {
      showAddForm.value = !showAddForm.value
    }
    return {
      t,
      addressesList,
      showAddForm,
      onShowAddForm
    }
  }
}
</script>

